import React, { Fragment, useState } from 'react';
import classNames from 'classnames';
import { HashRouter as Router, NavLink } from 'react-router-dom';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function SideMenu(props) {
  const { data = [], title, className } = props;
  const [showValue, setShowValue] = useState(false);
  //
  const showSideMenu = () => {
    setShowValue(true);
  };
  const cls = classNames(className, `${Variable}-sideMenu`, {
    [`is-hidden`]: showValue,
  });
  // 添加下标
  data.map((item, index) => {
    return (item['I_Love_You'] = index);
  });
  // 通过url路径
  let url = window.location.hash.split('#')[1].split('/');
  url.shift();
  return (
    <Fragment>
      <div className={cls}>
        <span
          className={`${Variable}-sideMenu__bg`}
          onClick={showSideMenu}
        ></span>
        {!!title && (
          <div className={`${Variable}-sideMenu__title`}>{title}</div>
        )}
        <div className={`${Variable}-sideMenu__list`}>
          <Router>
            {data.map((item, index) => {
              let path = item.path.split('/');
              path.shift();
              let actived;
              if (String(url) !== '') {
                path.map((subItem, subIndex) => {
                  subItem === url[subIndex] ||
                  (url[subIndex] === undefined &&
                    url[subIndex - 1] === path[subIndex - 1] &&
                    index === 0)
                    ? (actived = true)
                    : (actived = false);
                  return actived;
                });
              } else {
                actived = index === 0;
              }
              return (
                <NavLink
                  className={classNames(`${Variable}-sideMenu__item`, {
                    'is-actived': actived,
                  })}
                  activeClassName="is-actived"
                  to={item.path}
                  key={index}
                >
                  <span className={`${Variable}-sideMenu__item-title`}>
                    {item.title}
                  </span>
                </NavLink>
              );
            })}
          </Router>
        </div>
      </div>
    </Fragment>
  );
}
