import React, { Fragment } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function Container(props) {
  const {
    theme,
    className, // 自定义类名
    style, // 自定义样式
    full = false,
    ...rest // 自定义其他属性
  } = props;
  // const [dataTheme, setDataTheme] = useState('light');
  return (
    <Fragment>
      <div
        className={classNames(
          className,
          `${Variable}-container ${full ? 'is-full' : ''}`,
          theme === 'black' ? `color-black` : '',
          theme === 'white' ? `color-white` : ''
        )}
        {...rest}
      >
        {props.children}
      </div>
    </Fragment>
  );
}
function Header(props) {
  const {
    logo,
    title,
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  return (
    <Fragment>
      <div
        className={classNames(className, `${Variable}-container__header`)}
        {...rest}
      >
        <div
          className={classNames(
            className,
            `${Variable}-container__header-inner`
          )}
        >
          <div className={`${Variable}-container__header-logo`}>
            {!!logo && (
              <div className={`${Variable}-container__header-logo--img`}>
                <img src={logo} alt={title} />
              </div>
            )}
            {!!title && (
              <div className={`${Variable}-container__header-logo--title`}>
                {title}
              </div>
            )}
          </div>
        </div>
      </div>
    </Fragment>
  );
}
function Body(props) {
  const {
    className, // 自定义类名
    full,
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(className, `${Variable}-container__body`, {
    'is-full': full,
  });
  return (
    <Fragment>
      <div className={cls} {...rest}>
        <div className={`${Variable}-container__body-inner`}>
          {props.children}
        </div>
      </div>
    </Fragment>
  );
}
function Foot(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  return (
    <Fragment>
      <div
        className={classNames(className, `${Variable}-layout__content-footer`)}
        {...rest}
      >
        {props.children}
      </div>
    </Fragment>
  );
}
Container.Header = Header;
Container.Body = Body;
Container.Foot = Foot;
