/* eslint-disable react-hooks/exhaustive-deps */
import React, { Fragment, useState, useEffect } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';

export default function Animation(props) {
  const {
    type,
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(className, `${Variable}-animation`, {
    [`${Variable}-animation--${type}`]: !!type,
  });
  return (
    <div className={cls} style={{ ...style }} {...rest}>
      <div className={`${Variable}-animation__inner`}>{props.children}</div>
    </div>
  );
}
// CSS 加载 - 双圈
function Loading(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(className, `${Variable}-animation__loading`, {});
  return (
    <Fragment>
      <div className={cls} style={{ ...style }} {...rest}>
        <div className={`${Variable}-animation__loading-big`}></div>
        <div className={`${Variable}-animation__loading-small`}></div>
      </div>
    </Fragment>
  );
}
// CSS 加载 - 点点点
function Dot(props) {
  const {
    number = '3',
    type = 'block',
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(className, `${Variable}-animation__dot`);
  const sty = {
    animationDuration: `${
      0.13 * ((Number(number) * (Number(number) + 1)) / 2)
    }s`,
  };
  return (
    <Fragment>
      <div className={cls} style={{ ...style, ...sty }} {...rest}>
        {[...Array(Number(number))].map((item, index) => (
          <div
            className={classNames(
              className,
              `${Variable}-animation__dot-item`,
              {
                'is-circle': type === 'circle',
                'is-block': type === 'block',
              }
            )}
            key={`${Variable}-animation__dot-item--${index}`}
          ></div>
        ))}
      </div>
    </Fragment>
  );
}
