// Svg 进度条 - 圆形
function SvgCircle(props) {
  const {
    lineWidth = '10',
    circleWidth = '130',
    circleHeight = '130',
    value = 0, // 0~100
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;

  // 动画
  const circumference = parseInt(Math.PI * (circleWidth - lineWidth) + 1); // 计算圆圈的周长
  const [number, setNumber] = useState(circumference); // 设置状态，圆环的默认数值
  const [cvalue, setCvalue] = useState(0); // 参数默认值
  const activedFun = () => {
    setCvalue(cvalue + 1);
  };
  const animFun = () => {
    setNumber(circumference - parseInt((circumference / 100) * Number(value)));
  };
  useEffect(() => {
    let timer = null;
    if (cvalue < value) {
      timer = setInterval(() => {
        animFun();
        activedFun();
      }, 13);
    }
    return () => {
      clearInterval(timer);
    };
  }, [activedFun, animFun, cvalue, value]);
  const sty = {
    strokeDashoffset: number,
    transition: 'stroke-dashoffset 1s linear',
  };
  const cls = classNames(className, `${Variable}-animation__svgCircle`);
  return (
    <Fragment>
      <div className={cls} style={{ ...style }} {...rest}>
        <div className={`${Variable}-animation__svgCircle-value`}>
          {cvalue}%
        </div>
        <svg
          width={circleWidth}
          height={circleHeight}
          viewBox={`0 0 ${circleWidth} ${circleHeight}`}
        >
          <defs>
            <linearGradient x1="1" y1="0" x2="0" y2="0" id="gradient1">
              <stop offset="0%" stopColor="#f5a26d"></stop>
              <stop offset="100%" stopColor="#F7695F"></stop>
            </linearGradient>
          </defs>
          <g transform="matrix(0,-1,1,0,0,130)">
            <circle
              cx={circleWidth / 2}
              cy={circleWidth / 2}
              r={(circleWidth - lineWidth) / 2}
              strokeWidth={lineWidth}
              stroke="#f1f1f1"
              fill="none"
              strokeDasharray={`${circumference} ${circumference}`}
            ></circle>
            <circle
              cx={circleWidth / 2}
              cy={circleWidth / 2}
              r={(circleWidth - lineWidth) / 2}
              strokeWidth={lineWidth}
              stroke="url('#gradient1')"
              fill="none"
              strokeDasharray={`${circumference} ${circumference}`}
              strokeLinejoin="round"
              strokeLinecap="round"
              style={{ ...sty }}
            ></circle>
          </g>
        </svg>
      </div>
    </Fragment>
  );
}
// Js 进度条 - 圆形
function JsCircle(props) {
  const {
    value = 80,
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const [cvalue, setCvalue] = useState(0);
  const activedFun = () => {
    setCvalue(cvalue + 1);
  };
  useEffect(() => {
    let timer = null;
    if (cvalue < value) {
      timer = setInterval(activedFun, 13);
    }
    return () => {
      clearInterval(timer);
    };
  }, [cvalue]);
  const cls = classNames(className, `${Variable}-animation__jsCircle`);
  const bar = {
    transform: `rotate(${3.6 * (cvalue >= 50 ? 50 : cvalue)}deg) scale(.99)`,
    zIndex: `${cvalue >= 50 ? 3 : ''}`,
  };
  const barInner = {
    transform: `rotate(${3.6 * (cvalue >= 50 ? cvalue - 50 : 0)}deg)`,
  };
  return (
    <Fragment>
      <div className={cls} style={{ ...style }} {...rest}>
        <div className={`${Variable}-animation__jsCircle-bar`} style={bar}>
          <div
            className={`${Variable}-animation__jsCircle-bar__inner`}
            style={barInner}
          ></div>
        </div>
        <div className={`${Variable}-animation__jsCircle-value`}>{cvalue}%</div>
      </div>
    </Fragment>
  );
}

// Js 进度条 - 斑马纹
function Striped(props) {
  const {
    actived = 'start',
    width = '0',
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const [cwidth, setCwidth] = useState(0);
  const activedFun = () => {
    setCwidth(cwidth + 1);
  };
  useEffect(() => {
    let timer = null;
    if (cwidth < width) {
      timer = setInterval(activedFun, 13);
    }
    return () => {
      clearInterval(timer);
    };
  }, [cwidth]);
  const cls = classNames(className, `${Variable}-animation__striped`, {
    [`is-${actived}`]: !!actived,
  });
  return (
    <Fragment>
      <div className={cls} style={{ ...style }} {...rest}>
        <div
          className={`${Variable}-animation__striped-value`}
          style={{ width: `${cwidth}%` }}
        ></div>
      </div>
    </Fragment>
  );
}

// Js 进度条 - 水波纹
function Ripple(props) {
  const {
    value = 0,
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const [top, setTop] = useState(0);
  const stripedFun = () => {
    setTop(top + 1);
  };
  useEffect(() => {
    let timer = null;
    if (top < value) {
      timer = setInterval(stripedFun, 30);
    }
    return () => {
      clearInterval(timer);
    };
  }, [top]);
  const cls = classNames(className, `${Variable}-animation__ripple`);
  return (
    <Fragment>
      <div className={cls} style={{ ...style }} {...rest}>
        <div
          className={`${Variable}-animation__ripple-bg`}
          style={{ top: `${100 - Number(top)}%` }}
        ></div>
        <div className={`${Variable}-animation__ripple-value`}>{top}%</div>
      </div>
    </Fragment>
  );
}

// 水滴
