import React, { Fragment } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function Card(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(`${Variable}-card`, className);
  return (
    <Fragment>
      <div className={cls} {...rest}  style={{ ...style }}>
        {props.children}
      </div>
    </Fragment>
  );
}
function Header(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(`${Variable}-card__header`, className);
  return (
    <div className={cls} {...rest} style={{ ...style }}>
      <div className={`${Variable}-card__title`}>{props.children}</div>
    </div>
  );
}
function Body(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(`${Variable}-card__body`, className);
  return (
    <div className={cls} {...rest} style={{ ...style }} >
      {props.children}
    </div>
  );
}
function Foot(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  return (
    <div className={`${Variable}-card__foot`} {...rest}>
      {props.children}
    </div>
  );
}
Card.Header = Header;
Card.Body = Body;
Card.Foot = Foot;
