import React, { Fragment } from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { Variable } from '../Utility/Utility';
import './index.scss';
Bubble.propTypes = {
  children: PropTypes.any,
  content: PropTypes.any,
  trigger: PropTypes.oneOf(['click', 'hover', 'focus', 'empty']),
  // placement: PropTypes.oneOf([
  //     'auto-start',
  //     'auto',
  //     'auto-end',

  //     'top-start',
  //     'top',
  //     'top-end',

  //     'right-start',
  //     'right',
  //     'right-end',

  //     'bottom-start',
  //     'bottom',
  //     'bottom-end',

  //     'left-start',
  //     'left',
  //     'left-end',
  // ]),
};
const placementToDirection = {
  left: 'right',
  right: 'left',
  top: 'bottom',
  bottom: 'top',
};
// 获取箭头的位置
function getArrowPlacement(placement) {
  const [direction, align] = placement.split('-');

  return [
    `${Variable}-bubble-${placementToDirection[direction]}`,
    align ? `${Variable}-bubble-${align}` : null,
  ];
}
function Bubble(props) {
  const {
    content,
    children,
    placement,
    className,
    trigger = 'hover',
    show = false,
    ref,
    theme,
  } = props;
  const [directionCls, alignCls] = getArrowPlacement(placement);
  return (
    <Fragment>
      {children}
      {content && (
        <div
          className={classNames(
            `${Variable}-bubble`,
            directionCls,
            alignCls,
            className,
            trigger === 'click' ? 'is-click' : '',
            show ? 'is-actived' : '',
            theme === 'error' ? `${Variable}-bubble-error` : '',
            theme === 'dark' ? `${Variable}-bubble-dark` : ''
          )}
          ref={ref}
          data-placement={placement}
        >
          <div className={`${Variable}-bubble__inner`}>
            <div className={`${Variable}-bubble__content`}>{content}</div>
          </div>
        </div>
      )}
    </Fragment>
  );
}
export default Bubble;
