import { Content, Anchor, Card } from '../../components/Fule';
import classNames from 'classnames';
import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';
import './index.scss';
import Word from './word';

export default function Dome1(props) {
  const path = window.location.hash;

  return (
    <Content>
      <Content.Body
        aside={
          <Anchor aside>
            {Word.map((item, index) => (
              <Anchor.Item
                path={item.cid}
                cid={item.cid}
                key={`anchor-${index}`}
              >
                {item.title}
              </Anchor.Item>
            ))}
          </Anchor>
        }
      >
        {Word.map((item, index) => (
          <Card
            id={`${item.cid}`}
            key={index}
            className={classNames(
              'utility-h3',
              `#${item.cid}` === path ? 'is-actived' : ''
            )}
          >
            <Card.Header>{item.title}</Card.Header>
            <Card.Body>
              <Sitem value={item.data} />
            </Card.Body>
          </Card>
        ))}
      </Content.Body>
    </Content>
  );
}
function Sitem(props) {
  const {
    value, // 自定义类名
  } = props;
  return (
    <ReactMarkdown
      children={value}
      components={{
        code({ node, inline, className, children, ...props }) {
          const match = /language-(\w+)/.exec(className || '');
          return !inline && match ? (
            <SyntaxHighlighter
              children={String(children).replace(/\n$/, '')}
              style={vscDarkPlus}
              language={match[1]}
              PreTag="div"
              className="fl-syntaxHighlighter--div"
              {...props}
            />
          ) : (
            <code className={className} {...props}>
              {children}
            </code>
          );
        },
      }}
    />
  );
}
