import React from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';

export default function Item(props) {
  const {
    children,
    urlPath, // 浏览器链接后缀名带参数
    cid = '', // 锚点
    activedSelect = () => {},
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  // let activedValue = 1; // 全局变量
  const togele = () => {
    // activedValue = Number(index);
    activedSelect(cid);
    return;
  };
  const path = window.location.hash === '' ? `#id1` : window.location.hash; // 浏览器链接后缀名
  const cls = classNames(`${Variable}-anchor__item`, cid, className, {
    'is-actived': path === `#${cid}`,
  });
  return (
    <a
      href={`#${cid}`}
      className={cls}
      style={style}
      {...rest}
      onClick={togele}
    >
      <span className={`${Variable}-anchor__item-text`}>{children}</span>
    </a>
  );
}
