import React, { Fragment, useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import { Manager, Reference, Popper } from 'react-popper';
import PropTypes from 'prop-types';
import Bubble from '../Bubble';
import { Variable } from '../Utility/Utility';
import './index.scss';
Popover.propTypes = {
  children: PropTypes.any,
  content: PropTypes.any,
  trigger: PropTypes.oneOf(['click', 'hover', 'focus', 'empty']),
  // placement: PropTypes.oneOf([
  //     'auto-start',
  //     'auto',
  //     'auto-end',

  //     'top-start',
  //     'top',
  //     'top-end',

  //     'right-start',
  //     'right',
  //     'right-end',

  //     'bottom-start',
  //     'bottom',
  //     'bottom-end',

  //     'left-start',
  //     'left',
  //     'left-end',
  // ]),
};
// const placements = ['top', 'right', 'bottom', 'left'];

const modifiers = [
  {
    name: 'flip',
    enabled: false,
  },
  {
    name: 'hide',
    enabled: false,
  },
];

const popperModifiers = [
  ...modifiers,
  {
    name: 'arrow',
    options: {
      padding: 5,
    },
  },
  {
    name: 'offset',
    options: {
      offset: [0, 14],
    },
  },
];

const mainModifiers = [
  ...popperModifiers,
  // We can't use adaptive styles with CSS transitions
  {
    name: 'computeStyles',
    options: {
      adaptive: false,
    },
  },
];

function Popover(props) {
  // const [activePlacement, setActivePlacement] = useState(
  //   placements[Math.floor(Math.random() * placements.length)]
  // );
  const {
    className,
    content,
    children,
    placement,
    trigger = 'hover',
    show = false,
    style,
    theme,
  } = props;
  // 设置空的定时器变量
  let timer = null;
  const [bubble, setBubble] = useState(show);
  let swithBubble;
  let enterBubble;
  let leaveBubble;
  if (trigger === 'click') {
    swithBubble = () => setBubble(!bubble);
  } else {
    enterBubble = () => setBubble(true);
    leaveBubble = () => setBubble(false);
  }
  // 点击body 隐藏
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const hide = () => {
    timer = setTimeout(() => {
      setBubble(false);
    }, 10);
  };
  useEffect(() => {
    bubble && document.querySelector('#root').addEventListener('click', hide);
    return () => {
      document.querySelector('#root').removeEventListener('click', hide);
      clearTimeout(timer);
    };
  }, [bubble, hide, timer]);
  return (
    <Fragment>
      <Manager>
        <Reference>
          {({ ref }) => (
            <Fragment>
              <div
                className={`${Variable}-popover`}
                onMouseLeave={leaveBubble}
                onMouseEnter={enterBubble}
                onClick={swithBubble}
                ref={ref}
                style={{ ...style }}
              >
                {children}
              </div>
            </Fragment>
          )}
        </Reference>
        <Popper placement={placement} modifiers={mainModifiers}>
          {({ ref, style, placement, arrowProps }) =>
            ReactDOM.createPortal(
              <div
                className={classNames(
                  `${Variable}-popover__warp`,
                  bubble ? 'is-actived' : ''
                )}
                ref={ref}
                style={style}
                data-placement={placement}
              >
                <Bubble
                  placement={placement}
                  content={content}
                  className={className}
                  show={bubble}
                  trigger={trigger}
                  theme={theme}
                ></Bubble>
              </div>,
              document.querySelector('#popup')
            )
          }
        </Popper>
      </Manager>
    </Fragment>
  );
}

export default Popover;
