const digitChar = ['', ...'一二三四五六七八九'];
const posChar = ['', ...'十百千万   亿'];
const placeholder = '零';

function ChineseNumeralUnder1(digits) {
  const revDigits = String(digits).split('').reverse();
  let result = '';
  for (let pos = 0; pos < revDigits.length; pos++) {
    const digit = Number(revDigits[pos]);
    if (digit) {
      result = posChar[pos] + result;
    }
    if (digit !== 1 || pos !== 1) {
      result = digitChar[digit] + result;
    }
    if (!digit && pos && result && !result.startsWith(placeholder)) {
      if (result.startsWith('undefined')) {
        result = 'undefined';
      }
      result = placeholder + result;
    }
  }

  return result;
}

function ChineseNumeralUnder2(digits) {
  let highPart = ChineseNumeralUnder1(digits.slice(-8, -4));
  if (highPart) {
    highPart += posChar[4];
  }
  const lowPart = ChineseNumeralUnder1(digits.slice(-4));
  return highPart + lowPart;
}

export function ChineseNumeral(digits) {
  let fragment = '';
  const result = [];
  for (const digit of String(digits).split('').reverse()) {
    if (fragment.length === 8) {
      result.unshift(ChineseNumeralUnder2(fragment));
      fragment = '';
    }
    fragment = digit + fragment;
  }
  result.unshift(ChineseNumeralUnder2(fragment));
  if (result[0].startsWith(placeholder)) {
    result[0] = result[0].slice(1);
  }
  return result.join(posChar[8]) || placeholder;
}
