import React, { Fragment } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import SideMenu from '../SideMenu';
import './index.scss';
export default function Content(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  return (
    <Fragment>
      <div
        className={classNames(className, `${Variable}-layout__content`)}
        {...rest}
      >
        {props.children}
      </div>
    </Fragment>
  );
}
function Header(props) {
  const {
    ceiling,
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  return (
    <Fragment>
      <div
        className={classNames(className, `${Variable}-layout__content-header`, {
          ceiling,
        })}
        {...rest}
      >
        {props.children && (
          <div className={`${Variable}-layout__content-header--title`}>
            {props.children}
          </div>
        )}
      </div>
    </Fragment>
  );
}
function Body(props) {
  const {
    className, // 自定义类名
    subMenu, // 开启二级菜单
    aside,
    full, // 高度100%
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(className, `${Variable}-layout__content-body`, {
    'is-full': full,
    'has-subMenu': subMenu,
  });
  return (
    <Fragment>
      <div className={cls} {...rest}>
        {!!subMenu && (
          <div className={`${Variable}-layout__content-body--sideMenu`}>
            <SideMenu data={subMenu} />
          </div>
        )}
        {!!aside && (
          <div
            className={classNames(
              className,
              `${Variable}-layout__content-body--side`
            )}
            {...rest}
          >
            {aside}
          </div>
        )}
        <div className={`${Variable}-layout__content-body--scroll`}>
          <div className={`${Variable}-layout__content-body--inner`}>
            {props.children}
          </div>
        </div>
      </div>
    </Fragment>
  );
}
function Foot(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  return (
    <Fragment>
      <div
        className={classNames(className, `${Variable}-layout__content-footer`)}
        {...rest}
      >
        {props.children}
      </div>
    </Fragment>
  );
}
function Side(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  return (
    <Fragment>
      <div
        className={classNames(
          className,
          `${Variable}-layout__content-body__side`
        )}
        {...rest}
      >
        {props.children}
      </div>
    </Fragment>
  );
}
Content.Header = Header;
Content.Body = Body;
Content.Body.Side = Side;
Content.Foot = Foot;
