import React, { useState, useRef, useEffect } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import Item from './item';
import './index.scss';

export default function Anchor(props) {
  const {
    children, // 子代
    ceiling, // 顶部
    fixed = false, // 固定在顶部
    card, // 卡片类型
    aside, // 测导航
    onClick = () => {},
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;

  // 滚动后固定在顶部
  const [fixedValue, setFixedValue] = useState(fixed);
  const anchoRef = useRef(null);
  const headerFixedAni = () => {
    const anchorRect = anchoRef.current;
    const scrollTop =
      document.documentElement.scrollTop ||
      window.pageYOffset ||
      document.body.scrollTop; // 滚动条位置
    if (scrollTop > anchorRect.offsetTop && !fixedValue) {
      setFixedValue(true);
    }
    if (scrollTop < anchorRect.clientHeight && fixedValue) {
      setFixedValue(false);
    }
  };
  useEffect(() => {
    window.addEventListener('scroll', headerFixedAni);
    return () => {
      window.removeEventListener('scroll', headerFixedAni);
    };
  });

  // class
  const cls = classNames(`${Variable}-anchor`, className, {
    fixed: fixedValue,
    card,
    ceiling,
    aside: 'style-aside',
  });

  return (
    <div
      className={cls}
      style={style}
      {...rest}
      ref={anchoRef}
      onClick={onClick}
    >
      <div className={`${Variable}-anchor__list`}>
        {props.children}
        {/* {children.map((item, index) => (
            <Item cid={item.props.cid} className={item.props.className}>{item.props.children}</Item>
          ))} */}
      </div>
    </div>
  );
}
Anchor.Item = Item;
